@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,500;1,100&display=swap);
body {
  margin: 0;
  font-family:'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(250, 250, 250);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
}



.text-input-container {
    box-sizing: border-box;
    max-width: 1000px;
    min-width: 300px;
    width: 70%;
    background-color: rgb(250, 250, 250);
    border-radius: 10px;
    margin: 1.5em auto;
    box-shadow: 2px 2px 5px rgba(150, 150, 150, 0.5);
    padding:1em 0;
}


.text-input {
    height: 2em;
    width: 75%;
    border: none;
    outline:none;
    background-color: transparent;
    border-bottom: 1px solid rgba(150, 150, 150, 0.25);
    font-size: 1em;
}

.btn-submit {
    cursor: pointer;
    height: 2.5em;
    width: 15%;
    border: 1px solid dodgerblue;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: dodgerblue;
    color: floralwhite;
    transition: 350ms ease-in;
}

.btn-submit:hover {
    background-color:rgb(250, 250, 250);
    border: 1px solid dodgerblue;
    color: dodgerblue;
    font-weight: 700;
    transition: 350ms ease-out;
}




@media screen and (max-width:500px) {
    .btn-submit  {
        display: block;
        width: 90%;
        margin: 0.5em auto;
        border-radius: 5px;
        margin-top: 1em;
    }

    .text-input-container {
        width: 90%;
        padding: 0.5em 0;
    }

    .text-input {
        width: 90%;
    }

}


.msg-alert-main {
    background-color: rgba(100, 100, 100, 0.5);
    position: fixed;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    height: 100vh;
    width: 100vw;
    top: 0;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
}

.alert-card {
    width: 80%;
    min-width: 300px;
    height: 10em;
    background-color: seashell;
    top: calc((100% - 10em)/2);
    border-radius: 0.75em;
    max-width: 400px;
    min-width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-around
}

.alert-card-title {
    width: 80%;
    margin: 0;
    text-align: left;
    font-weight: 300;
    font-size: 1.25em;
    align-self:center;
}

.alert-card-btn {
    padding: 1em 1em;
    width: 80%;
    align-self:center;
    border: 1px solid dodgerblue;
    border-radius: 0.5em;
    background-color: dodgerblue;
    color: white;
    font-size: 1em;
    transition: 350ms ease-out;
}

.alert-card-btn:hover {
    background-color: seashell;
    border: 1px solid dodgerblue;
    color: dodgerblue;
    font-weight: 700;
    transition: 350ms ease-out;
}


.order-btns {
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end
}

.delete-btn {
    margin: 0 5px;
    width: 25%;
    padding: 10px;
    align-self: center;
    border: 1px solid red;
    border-radius: 0.5em;
    background-color: red;
    color: white;
    font-size: 1em;
    transition: 350ms ease-out;
}

.cancel-btn {
    margin: 0 5px;
    width: 25%;
    padding: 10px;
    align-self: center;
    border: 1px solid gray;
    border-radius: 0.5em;
    background-color: gray;
    color: white;
    font-size: 1em;
    transition: 350ms ease-out;

}

.delete-btn:hover {

    border: 1px solid red;
    background-color: white;
    color: red;
    font-weight: 600;
    transition: 350ms ease-out;
}

.cancel-btn:hover {

    border: 1px solid grey;
    background-color: white;
    color: grey;
    font-weight: 600;
    transition: 350ms ease-out;
}







@media screen and (max-width:500px) {

    .alert-card {
        font-size: 1em;

    }

    .alert-card-btn {
        font-size: 1em;
    }

    .order-btns {
        width: 90%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
    }

    .delete-btn {
        width: 85%;
        margin: 2.5px 0;
    }
    
    .cancel-btn {
        width: 85%;
        margin: 2.5px 0;
    }


}
